import * as React from "react";
import { Toast, InputItem, TextareaItem, Picker, List } from 'antd-mobile'
import qs from 'qs'




import { applyCertificate } from '../../api/consumer'
import { getAllCity } from '../../api/index'
import ConsumerPanl from '../../components/consumer/consumerPanl'

import BottomBtn from '../../components/btn/bottomBtn'

import './consumer.less'
interface ISprops {
    [propsName: string]: any
}
interface IState {
    [statesName: string]: any
}

export default class Certificate extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        this.state = {
            course_id: '',
            member_id: '',
            title: '',
            consignee: '',
            consignee_mobile: '',
            hasError: false,
            detail_address: '',
            cityList: [],
            checkedCity: [],
            extraText: ' ',
            btnDisabled: false
        }
    }
    getCityList() {
        getAllCity().then(res => {
            if (!res.status) {
                this.setState({
                    cityList: res.data
                })
            }
        })
    }
    getSearchCode() {
        let search = this.props.location.search;
        let data = qs.parse(search.substring(1));
        this.setState({
            course_id: data.course_id,
            member_id: data.member_id,
            title: data.title
        })
        this.getCityList()
    }
    componentWillMount() {
        document.title = '填写收件地址';
        this.getSearchCode()
    }
    changePhone = (value: any) => {
        let phones = /^1\d{10}$/;
        if (!phones.test(value)) {
            this.setState({
                hasError: true,
            });
        } else {
            this.setState({
                hasError: false,
            });
        }
        this.setState({
            consignee_mobile: value,
        });
    }
    onErrorClick = () => {
        //手机号验证设置
        if (this.state.hasError) {
            Toast.info('请输入11位有效手机号');
        }
    }
    checkEmpty(val, text) {
        if (val.replace(/\s/g, '').length === 0) {
            Toast.fail(text + '不能为空',2);
            return false;
        }
        return true
    }
    checkCity(list) {
        if(list.length < 3) {
            Toast.fail('地区不能为空',2);
            return false;
        }
        return true;

    }
    pickerCity = (e) => {
        let arr:any = [];
        const { cityList } = this.state;
        this.setState({checkedCity:e})
        cityList.filter( (el: any,index) => {
            if(el.value === e[0]) {
                arr.push(el.label)
                el.children.filter( (el1,index1) => {
                    if(el1.value === e[1]) {
                        arr.push(el1.label)
                        el1.children.filter( (el2,index2) => {
                            if(el2.value === e[2]) {
                                arr.push(el2.label)
                            }
                        })
                    }
                })
            }
        })
        this.setState({
            extraText: arr.join(',')
        })
    }
    submitClick =  async () => {
        this.setState({btnDisabled: true})
        const { course_id, member_id, checkedCity, consignee, consignee_mobile, detail_address } = this.state;

        if (this.checkEmpty(consignee, '姓名')  && this.checkEmpty(detail_address, '详细地址') && this.checkCity(checkedCity) && this.state.hasError !== true) {
            let params = {
                course_id,
                member_id,
                consignee,
                consignee_mobile,
                detail_address,
                province_id: checkedCity[0],
                city_id: checkedCity[1],
                county_id: checkedCity[2],
            }
            applyCertificate(params).then( res => {
                this.setState({btnDisabled: false})
                if(!res.status) {
                    Toast.info('申请成功')
                    this.props.history.push("/consumer/index")
                } else {
                    Toast.fail(res["message"],2)
                }
            })
        } else {
            this.setState({btnDisabled: false})
        }

    }
    render() {
        const { title, consignee, consignee_mobile, detail_address, cityList, checkedCity, extraText, btnDisabled } = this.state;
        return (
            <div className="get-certificate">
                <ConsumerPanl title="课程名称">
                    <p>{title}</p>
                </ConsumerPanl>

                <div className="con">
                    <InputItem
                        placeholder="请输入收件人姓名"
                        className="input-item"
                        value={consignee}
                        onChange={(e) => { this.setState({ consignee: e }) }}
                    >* 姓名</InputItem>

                    <InputItem
                        clear
                        placeholder="请输入收件人手机号"
                        className="input-item"
                        value={consignee_mobile}
                        onChange={this.changePhone}
                        error={this.state.hasError}
                        onErrorClick={this.onErrorClick}
                    >* 手机</InputItem>

                    <Picker
                        extra={extraText}
                        className="input-item"
                        data={cityList}
                        onOk={(e) =>  this.pickerCity(e)}
                    >
                        <List.Item arrow="horizontal">* 地区</List.Item>
                    </Picker>
                    <TextareaItem
                        title="* 地址"
                        placeholder="如道路、门牌号、小区、楼栋号等"
                        data-seed="logId"
                        autoHeight
                        value={detail_address}
                        onChange={(val) => { this.setState({ detail_address: val }) }}
                    />

                </div>

                <BottomBtn title="提交" btnDisabled={btnDisabled} click={this.submitClick} />
            </div>
        )
    }

}
